import {Component, OnInit, Inject} from '@angular/core';
import {TravelService} from '../services/travel.service';
import {FormService} from '../services/form.service';
import {ActivatedRoute} from '@angular/router';
import {VehicleService} from '../services/vehicle.service';
import {DriverService} from '../services/driver.service';
import {Router} from '@angular/router';
import {MatSnackBar} from '@angular/material';
import {MatDialog, MatDialogRef, MAT_DIALOG_DATA} from '@angular/material';
import {DenielJustificationDialogComponent} from '../deniel-justification-dialog/deniel-justification-dialog.component';

@Component({
  selector: 'app-allocation',
  templateUrl: './allocation.component.html',
  styleUrls: ['./allocation.component.scss']
})
export class AllocationComponent implements OnInit {

  protected model: any = {};
  protected isLinear = false;
  protected checked = false;
  protected terminal = false;
  protected id;

  protected availableVehicles = [];
  protected busyVehicles = [];
  protected travelsByVehicle = [];
  protected vehicles = [];
  protected vehiclesHeader = ['Veículo', 'Placa', 'Marca', 'Capacidade'];

  protected drivers = [];
  protected driversHeader = ['Nome', 'Cpf', 'Cnh', 'Registro'];

  protected selectedDriver: number;
  protected selectedVehicle: number;
  protected selectedVehicleAvailable = -1;
  protected selectedVehicleWithTravel: number;
  protected selectedTravel = {};
  protected selectedTravelID = -1;
  protected isCarona = false;
  protected formCarona = {};

  protected isLoading: boolean;
  protected isLoadingAccordion: boolean;

  animal: string;
  name: string;

  constructor(private travelService: TravelService,
              private route: ActivatedRoute,
              private formService: FormService,
              private vehicleService: VehicleService,
              private driverService: DriverService,
              private snackBar: MatSnackBar,
              private router: Router,
              public justificationDialog: MatDialog) {
    this.id = this.route.snapshot.params['id'];
    this.isLoading = true;
    this.isLoadingAccordion = true;
  }

  ngOnInit() {
    this.formService.getById(this.id).subscribe((data) => {
        this.model = data;
        this.model.departureHour = data.departureHour.slice(11);
        this.model.returnHour = data.returnHour.slice(11);

        this.getVehiclesWithTravels();
        this.getAvailableVehicles();
        this.getAllDrivers();

        this.isLoading = false;

      }, error => {
        console.error(error);
      }
    );
  }

  getAllDrivers() {
    this.driverService.driversObs.subscribe((data) => {
      this.drivers = data;
    });
  }

  getAvailableVehicles() {
    this.vehicleService.getAvailables(this.id).subscribe((data) => {
      this.availableVehicles = data;
    });
  }

  getVehiclesWithTravels() {
    this.vehicleService.getVehiclesWithTravels(this.model.travelDate).subscribe((data2) => {
      this.busyVehicles = data2;
    });
  }

  selectDriver(id: number) {
    this.selectedDriver = id;
    console.log(this.selectedDriver);
  }

  selectVehicleAvailable(id: number) {
    this.isCarona = false;
    this.selectedVehicleWithTravel = -1;
    this.selectedTravelID = -1;
    this.selectedTravel = {};

    if (this.selectedVehicleAvailable === id) {
      this.selectedVehicleAvailable = -1;
    } else {
      this.selectedVehicleAvailable = id;
    }

    console.log(id, this.selectedVehicleAvailable, this.selectedVehicleAvailable);

    // console.log(id, this.selectedVehicleAvailable, this.selectedVehicleAvailable);
    //
    // if (this.selectedVehicleAvailable === -1 || this.selectedVehicleAvailable !== id) {
    //   this.selectedVehicleAvailable = id;
    // } else {
    //   console.log(id, this.selectedVehicleAvailable, this.selectedVehicleAvailable);
    //   this.selectedVehicleAvailable = -1;
    // }
    //
    // console.log(this.selectedVehicleAvailable);
  }

  selectVehicleWithTravels(id: number) {
    this.isCarona = true;
    this.selectedVehicleAvailable = -1;
    this.selectedVehicleWithTravel = id;
  }

  updateForm() {
    this.isLoading = true;
    const id = this.model.id;
    const form = Object.assign({}, this.model);
    form.travelDate += ' ' + form.departureHour;
    form.returnDate += ' ' + form.returnHour;
    form.requestDate += ' ' + form.returnHour;
    form.returnHour = form.returnDate;
    form.departureHour = form.travelDate;

    this.formService.update(id, form).subscribe((data) => {
      const snackBarRef = this.snackBar.open('Solicitação atualizada!', 'OK', {duration: 5000});
      this.getAvailableVehicles();
      this.getVehiclesWithTravels();

      this.isLoading = false;

    }, error => {
      const snackBarRef = this.snackBar.open(error.errors[0], 'ENVIAR NOVAMENTE', {duration: 5000});
    });
  }

  confirmTravel() {

    let form;

    if (this.selectedVehicleWithTravel !== -1) {
      if (this.selectedTravelID !== -1) {
        form = {
          formId: +this.id,
          travelId: this.selectedTravelID
        };

        this.travelService.ride(form).subscribe(data => {
          const snackBarRef = this.snackBar.open('Solicitação aceita e a carona foi confirmada!', 'OK', {duration: 5000});
          this.router.navigate(['/home/travels']);
        }, error => {
          const snackBarRef = this.snackBar.open(error.errors[0], 'ENVIAR NOVAMENTE', {duration: 5000});
        });
      } else {
        const snackBarRef = this.snackBar.open('A viagem de carona não foi selecionada', 'OK', {duration: 15000});
      }
    } else {
      form = {
        formId: +this.id,
        vehicle: this.selectedVehicleAvailable,
        driver: this.selectedDriver
      };

      // this.selectedVehicle = this.selectedVehicleAvailable;
      this.travelService.create(form).subscribe(data => {
        const snackBarRef = this.snackBar.open('Solicitação aceita e viagem confirmada!', 'OK', {duration: 5000});
        this.router.navigate(['/home/travels']);
      }, error => {
        const snackBarRef = this.snackBar.open(error.errors[0], 'ENVIAR NOVAMENTE', {duration: 5000});
      });
    }
  }

  changeDeparturePoint(point: string) {
    if (point !== this.model.departurePoint) {
      this.model.departurePoint = point;
    }
  }

  removeForm(id: number) {
    this.formService.deleteById(id).subscribe(data => {
      const snackBarRef = this.snackBar.open('Formulário removido com sucesso!', 'OK', {duration: 5000});
      this.router.navigate(['/home/solicitations']);
    }, error => {
      const snackBarRef = this.snackBar.open(error.errors[0], 'ENVIAR NOVAMENTE', {duration: 5000});
    });
  }

  showTravels(id: number) {
    this.travelsByVehicle = [];
    this.vehicleService.getTravelsFromVehicles(id, this.model.travelDate).subscribe((data) => {
      this.travelsByVehicle = data;
      this.isLoadingAccordion = false;
      console.log(this.isLoadingAccordion);
    });
  }

  selectTravel(travel: object, id: number, model: object) {
    if (this.selectedTravelID === -1) {
      this.selectedTravelID = id;
      this.formCarona = model;
      this.selectedTravel = travel;
    } else {
      this.selectedTravelID = -1;
      this.formCarona = {};
      this.selectedTravel = {};
    }
  }

  deniedForm(justification: string) {
    const form = {
      justificationDeniel: justification
    };
    this.formService.denied(this.id, form).subscribe((data) => {
      const snackBarRef = this.snackBar.open('Solicitação foi bloqueada!', 'OK', {duration: 5000});
      this.router.navigate(['/home/solicitations']);
    }, error => {
      const snackBarRef = this.snackBar.open(error.errors[0], 'ENVIAR NOVAMENTE', {duration: 5000});
    });
  }

  openDialog(): void {
    const loginRef = this.justificationDialog.open(DenielJustificationDialogComponent, {
      height: '40%',
      width: '50%'
    });
    loginRef.afterClosed().subscribe(justification => {
      if (justification) {
        this.deniedForm(justification);
      }
    });
  }
}



