import { Component, OnInit } from '@angular/core';
import { MatDialogRef } from '@angular/material';
import {ViewEncapsulation} from '@angular/core';

@Component({
  selector: 'app-deniel-justification-dialog',
  templateUrl: './deniel-justification-dialog.component.html',
  styleUrls: ['./deniel-justification-dialog.component.scss'],
  encapsulation: ViewEncapsulation.None

})
export class DenielJustificationDialogComponent implements OnInit {

  constructor(private dialogRef: MatDialogRef<DenielJustificationDialogComponent>) { }

  protected justification: string;

  ngOnInit() {
  }

  onSubmit(value) {
    this.dialogRef.close(this.justification);
  }

  closeDialog() {
    this.dialogRef.close();
  }

}



